.edge-links {
    position: fixed;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    gap: 10px; /* Space between icons */
}

.edge-link {
    color: #333;
    font-size: 24px;
    text-decoration: none;
}

.edge-link:hover {
    color: #007bff; /* Hover color */
}



